// Gift Card module

#module-psgiftcards-Giftcards {
	.page-content {
		margin: 0 15px; } }

// Homeslider

.slide-content {
	max-width: 600px; }

.slide-content h1,
.slide-content h2,
.slide-content h3,
.slide-content h4,
.slide-content h5,
.slide-content h6 {
	margin-bottom: $large-space;
	line-height: 50px; }

.slide-content h1 span,
.slide-content h2 span,
.slide-content h3 span,
.slide-content h4 span,
.slide-content h5 span,
.slide-content h6 span {
	font-family: $secondary-font;
	line-height: 50px; }


.slide-content p, .slide-content span {
	font-family: $primary-font;
	line-height: 21px; }


.slide-content .btn {
	margin-top: $large-space; }


