.main-menu {
	position: relative;
	margin-top: 15px;
	@include media-breakpoint-down(sm) {
		margin-top: 0; }

	.menu {
		margin: auto;
		max-width: 1110px;
		> ul {
			> li {
				display: inline-block;
				line-height: 28px;
				margin-left: $large-space;
				&:first-child {
					margin-left: 0; } } } }

	#_desktop_top_menu {
		#top-menu {
			min-height: 55px;
			border-top: 2px solid $gray-lighter;
			border-bottom: 2px solid $gray-lighter;
			margin-bottom: 0;
			padding: $small-space 0;
			@include media-breakpoint-between(md,lg) {
				padding: $small-space 15px; }

			a[data-depth="0"] {
				text-transform: uppercase;
				padding: 0;
				&:first-child {
					padding-left: 0; }

				&.internal-link {
					&:after {
						content: "\f078";
						font-family: $fa-font;
						font-size: 8px !important;
						position: relative;
						top: -2px;
						left: 7px;
						color: $gray; }

					&:hover, &:focus {
						&:after {
							content: "\f077";
							font-family: $fa-font; } } } }

			a:not([data-depth="0"]) {
				display: block;
				padding: $small-space;
				font-weight: 400; }

			.top-menu {
				width: 100%;
				&[data-depth="1"] {
					max-width: 1110px;
					margin: $extra-small-space auto 15px;
					font-weight: bold; }

				a.dropdown-submenu {
					color: $gray-darker;
					text-transform: uppercase;
					font-weight: bold; }

				a[data-depth="1"] {
					padding: 0 0 $extra-small-space 0;
					font-size: $font-size-sm;
					span {
						font-size: $font-size-sm; } }

				a[data-depth="2"], a[data-depth="2"] span {
					padding: 0;
					color: $gray;
					font-size: $font-size-xs;
					line-height: 24px;
					span {
						font-size: $font-size-xs;
						line-height: 24px;
						color: $gray; } } }

			.collapse {
				display: inherit; }

			.sub-menu {
				border: none;
				box-shadow: 0 6px 8px 0 rgba(0,0,0,.3);
				width: 100%;
				min-width: 100%;
				z-index: 18;
				position: absolute;
				left: 0;
				background: $white;
				display: none;
				padding-top: $extra-small-space;
				top: 55px !important;
				&.collapse {
					display: none; }

				ul[data-depth="1"] > li {
					float: left;
					margin: 0 $large-space;
					&:first-child {
						margin-left: 0; } } } } } }



